<template>
  <div class="article">
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 search"
      placeholder="请输入标题或关键词" v-model="keywords"
    ></b-input>

    <div class="content" v-for="article in search(keywords)">

      <router-link :to="'/article/'+article.permlink">
        <b-img :src="article.body | takeImage" fluid  class="courseimg"></b-img>
      </router-link>

      <div class="title">
        <router-link :to="'/article/'+article.permlink">
          <span style="font-size: 1.2rem">{{ article.title }}</span><br>
        </router-link>
      </div>
      <p class="posttime">
        @{{  article.nickname }}({{ article.addr | formatAddr }})
        <span style="margin-left: 1rem"> {{ timeFormat(article.created) }}</span>
      </p>
      <p class="text">{{ article.body | abstractFn }} ......</p>
      <!--<div style="clear:both;"></div>-->

    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

    <!--<div class="btn">-->
      <!--&lt;!&ndash;<b-button variant="outline-primary" @click="getNext">查看更多</b-button>&ndash;&gt;-->
    <!--</div>-->
  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
      name: "MyArticles",
      data(){
          return{
            keywords:'',
            author:'',
            permlink:'',
            isLoading: true,
          }
      },
      methods:{
        search(keywords){
          let articles = this.$store.state.articles
          let res = this.myarticlesFilter(articles)
          return res.filter(item => {
            if(item.title.includes(keywords) || item.keywords.includes(keywords)){
              // console.log(166, '查到了！')
              return item
            }
          })
        },
        myarticlesFilter(array){
          let myarticles = []
          array.forEach(item => {
            if(item.addr.toLowerCase() == this.$store.state.addr){
              //是作者
              console.log(99559, "是作者")
              myarticles.push(item)
            }
          })
          return myarticles
        },
        getNext(){
          let that = this
          that.isLoading = true
          let query = {tag: "hive-180932", limit: 20, start_author: this.author, start_permlink: this.permlink}
          this.steem.api.getDiscussionsByTrending(query, function(err, data) {
            data.forEach(post => {
              if (post.permlink != that.permlink) {
                that.posts.push(post)
              }
            })
            //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
            that.author = data[data.length - 1].author
            that.permlink = data[data.length - 1].permlink
            //将得到的数据存入vuex中
            that.$store.commit('saveHot', that.posts)
            that.isLoading = false
            if(data.length == 1){
              alert('没有更多了！')
            }
          })
        },

      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.postinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            console.log(777, "instance为空，是刷新页面")
            let instance = await that.getPostInstance()
            await that.getArticles(instance)
            that.isLoading = false
          } else{
            if(that.$store.state.articles == false){
              console.log(666, "如果文章更新！")
              //如果文章更新
              await that.getArticles(instance)
              that.isLoading = false
            } else{
              console.log(2222, "啥也没干！")
              that.isLoading = false
            }
          }
        }
        main()
      },

      filters:{
        changeTime(timestamp){
          // 比如需要这样的格式 yyyy-MM-dd hh:mm:ss
          let date = new Date(timestamp * 1000)
          let Y = date.getFullYear() + '-'
          let  M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
          let D = date.getDate() + ' '
          let h = date.getHours() + ':'
          let m = date.getMinutes() + ':'
          let s = date.getSeconds()
          return Y+M+D+h+m+s
        },
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },
        takeImage(text){
          let reg = /!\[(.*?)\]\((.*?)\)/
          // let reg = /\!\[.*?\)/
          let a = text.match(reg)
          if(a === null){
            return
          }
          return a[2]
        },
        abstractFn(res){
          if(!res){
            return ''
          }else{
            let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                    .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                    .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                    .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                    .replace(/http.*\w/g,'')                              //全局http连接
                    .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                    .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                    .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                    .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                    .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                    .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                    .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                    .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                    .replace(/\r\n/g,"")                                        //全局匹配换行
                    .replace(/\n/g,"")                                          //全局匹配换行
            // .replace(/\s/g,"")                                          //全局匹配空字符;
            return str.slice(0,180)
          }
        }

      },
    }
</script>

