<template>
  <div class="article">
     <div class="content" >

        <p class="posttime">
            <span style="margin-right: 1.5rem;margin-left: 2rem">
                悬赏号：{{ task.taskNumber }}</span>
                {{ timeFormat(task.created) }}
        </p>

         <p class="title">{{ task.name}}</p>

         <div class="text">
            金主： @{{  task.nickname }} ({{ task.promoter }})<br>
            赏金： {{ weiToEth(task.bonus)  }}  灵石<br>
            联系金主：{{ task.contact }}<br>
            任务描述：
             <mavon-editor
                     :value="task.content + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>

             <div v-if="task.state === '0'">
                 <b-form-group label="已申请的猎人名单：" v-for="hunter in task[5]">
                     <b-form-radio v-model="selected" name="some-radios" :value="hunter">{{ hunter }}</b-form-radio>
                 </b-form-group>
                 <div class="mt-3">中选猎人: <strong>{{ selected }}</strong></div>
             </div>

         </div>

         <!--任务进行中-->
         <div v-if="task.state === '2'" class="hunter">任务进行中.....
             <div v-if="leftTime > 0">任务剩余时间： {{ formatSeconds(leftTime) }}
                 <div class="btn">
                     <b-button variant="outline-primary" @click="hunterAFinished(task.uid)">提交任务</b-button>
                 </div>
             </div>
             <div v-else>任务已超时！</div>
         </div>

         <!--猎人已提交任务-->
         <div class="btn2" v-else-if="task.state === '3'" >
             您已提交任务，请等待金主确认或是退回！
             <div v-if="leftTime > 0">金主确认的剩余时间： {{ formatSeconds(leftTime)  }}</div>
         </div>

         <!--金主已确认-->
         <div class="btn2" v-else="task.state === '5'" ><br>
             金主已确认，恭喜您圆满完成任务并收到赏金！
         </div>

     </div>




      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "MyTaskUnit",
      data(){
          return{
            task: {},
            selected: '',
            isLoading: false,
            leftTime:'',

          }
      },
      methods:{
         taskFilter(){
              //获取单个任务
              let tasks = this.$store.state.bountys
              let taskNumber = this.$route.params.taskNumber
              tasks.forEach(item => {
                  if(item.taskNumber === taskNumber){
                      this.task = item

                      //任务剩余时间
                      let deadtime = parseInt(item.taskStart) + parseInt(item.taskDeadLine)
                      let timestamp = Date.parse(new Date()) * 1e-3
                      this.leftTime = deadtime - timestamp
                  }
              })
          },
         hunterAFinished(id){
             let _this = this
             let instance = this.$store.state.bountyinstance
             let addr = this.$store.state.addr
             async function main() {
                 let s = await instance.hunterAFinished(id, {from: addr})
                 console.log(555, s)
                 await _this.getTasks(instance)
                 _this.taskFilter()

                 //直接刷新当前页面
                 // _this.$router.go(0)

             }
             main()
          }

      },


      components: {
        Loading
      },
      computed: {
            prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },

        mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.taskFilter()
                    // await that.hashToString()
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.taskFilter()
                        // await that.hashToString()
                        that.isLoading = false
                    } else{
                        console.log(22333, "啥也没干！")
                        that.taskFilter()
                        // await that.hashToString()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{

      },
    }
</script>

