const getTasks = function(instance){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let s = await instance.id()
      let tasks = []
      for(let i = s; i > 0 ; i --){
        let s1 = await instance.getTask(i)
        let con = await that.ipfs.cat(s1[0])
        let t = that.utf8ToStr(con)
        let task = JSON.parse(t)
        task.uid = i
        task.promoter = s1[1]
        task.hunterA = s1[2]
        task.hunters = s1[3]

        task.reasonHashs = s1[4]

        let aa = []
        for(let i = 0; i < task.reasonHashs.length; i ++){
            let con = await that.ipfs.cat(task.reasonHashs[i])
            let t = that.utf8ToStr(con)
            let ss =  JSON.parse(t)
            aa.push(ss)
          }
        task.reasons = aa

        task.taskStart = s1[6]
        task.taskDeadLine = s1[7]
        task.state = s1[8]
        // console.log(56000, task)
        tasks.push(task)
      }
      //将得到的数据存入vuex中
      that.$store.commit('saveBountys', tasks)
      resolve()
    }
    main()
  })
}

export {getTasks}
