import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login'
import Admin from '../components/Admin'
import Index from '../components/Index'
import Article from "../components/Article"
import MyArticles from "../components/MyArticles"
import Market from '../components/Market'
import Myseller from '../components/Myseller'
import MysellerUint from '../components/MysellerUint'
import Mybuyer from '../components/Mybuyer'
import Bounty from '../components/Bounty'
import MyBounty from '../components/MyBounty'
import MyBountyUnit from '../components/MyBountyUnit'
import MyTask from '../components/MyTask'
import MyTaskUnit from '../components/MyTaskUnit'
import Order from '../components/Order'
import Task from '../components/Task'
import Post from '../components/Post'
import PostOrder from '../components/PostOrder'
import PostBounty from '../components/PostBounty'
import Edit from '../components/Edit'
import Collections from "../components/Collections"
import Collection from "../components/Collection"
import About from "../components/footers/About"
import Newbie from "../components/footers/Newbie"
import Wallet from "../components/Wallet"
import Test from "../components/Test"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },

  {
    path: '/article/:permlink',
    name: 'article',
    component: Article,
  },
  {
    path: '/market',
    name: 'market',
    component: Market,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/market/:orderNumber',
    name: 'order',
    component: Order,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/myseller',
    name: 'myseller',
    component: Myseller,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/myseller/:orderNumber',
    name: 'myselleruint',
    component: MysellerUint,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/mybuyer',
    name: 'mybuyer',
    component: Mybuyer,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/bounty',
    name: 'bounty',
    component: Bounty,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/bounty/:taskNumber',
    name: 'task',
    component: Task,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/myarticles',
    name: 'myarticles',
    component: MyArticles,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/mybounty',
    name: 'mybounty',
    component: MyBounty,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/mybounty/:taskNumber',
    name: 'mybountyunit',
    component: MyBountyUnit,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/mytask',
    name: 'mytask',
    component: MyTask,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/mytask/:taskNumber',
    name: 'mytaskunit',
    component: MyTaskUnit,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta:{
      requireAuth:true
    }
  },

  {
    path: '/test',
    name: 'test',
    component: Test,
  },


  {
    path: '/collections',
    name: 'collections',
    component: Collections,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/collection/@:author/:permlink',
    name: 'collection',
    component: Collection
  },

  {
    path: '/wallet',
    name: 'wallet',
    component: Wallet
  },

 {
    path: '/about',
    name: 'about',
    component: About
  },
  {
    path: '/newbie',
    name: 'newbie',
    component: Newbie
  },
  {
    path: '/post',
    name: 'post',
    component: Post,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/postorder',
    name: 'postorder',
    component: PostOrder,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/postbounty',
    name: 'postbounty',
    component: PostBounty,
    meta:{
      requireAuth:true
    }
  },
  {
    path: '/edit/:permlink',
    name: 'edit',
    component: Edit,
    meta:{
      requireAuth:true
    }
  },

]

const router = new VueRouter({
  mode: 'history',
  // mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
