const getArticles = function(instance){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let s = await instance.id()
      let articles = []
      for(let i = s; i > 0 ; i --){
        let s1 = await instance.get(i)
        let con = await that.ipfs.cat(s1[0])
        let t = that.utf8ToStr(con)
        let article = JSON.parse(t)
        article.uid = i
        article.voted = s1[2]
        article.amount = s1[3]
        articles.push(article)
      }
      //将得到的数据存入vuex中
      that.$store.commit('saveArticles', articles)
      resolve(articles)
    }
    main()
  })
}

export {getArticles}
