const getWalletInstance = function(){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let res = await that.axios.get('/LarkStone.json')
      // console.log(66799, res.data)
      let contract = that.TruffleContract(res.data)
      // console.log(123457, contract)
      contract.setProvider(that.web3Provider)
      // contract.setProvider(that.web3.currentProvider)
      let instance = await contract.deployed()
      // console.log(58988, instance)
      //将得到的数据存入vuex中
      that.$store.commit('saveWalletInstance', instance)
      resolve()
    }
    main()
  })
}

export {getWalletInstance}
