<template>
  <div id="content">
    <h1>{{ article.title }}</h1>

    <p class="posttime">
      @{{  article.nickname }}({{ article.addr }})
      <span style="margin-left: 1rem"> {{ timeFormat(article.created) }}</span>
    </p>

    <div id="textclass">
    <mavon-editor
     :value="article.body  + '<br>' + '<br>' + keywords + article.keywords.join('　')"
    :subfield = "prop.subfield"
    :defaultOpen = "prop.defaultOpen"
    :toolbarsFlag = "prop.toolbarsFlag"
    :editable="prop.editable"
    :scrollStyle="prop.scrollStyle"
    ></mavon-editor>
    </div>
    <br>
    <div v-if="!isMyself">
      <div v-if="!votedFlag" class="vote">
        赞赏：
        <span style="margin-right: 1rem">
      <b-button variant="outline-primary" @click="voteLevel1(article.uid)">{{ level1 }} 灵石</b-button>
      </span>
        <span style="margin-right: 1rem">
       <b-button variant="outline-primary" @click="voteLevel2(article.uid)">{{ level2 }} 灵石</b-button>
       </span>
        <b-button variant="outline-primary" @click="voteLevel3(article.uid)">{{ level3 }} 灵石</b-button>
      </div>
      <div v-else class="vote">
        已赞赏
      </div>
    </div>


  </div>
</template>

<script>

 export default {
      name: "Trend",
      data() {
        return {
          db:{},
          article: {},
          data:'',
          text:'',
          keywords: '关键词：　',
          votedFlag: false,
          isMyself: false,
          level1: '',
          level2: '',
          level3: '',
        }
      },

      methods:{
        articleFilter(){
          //获取单篇文章
          let articles = this.$store.state.articles
          let permlink = this.$route.params.permlink
          articles.forEach(item => {
            if(item.permlink === permlink){
              this.article = item
            }
          })
        },
        getArticleDone(){
          //对文章的一系列操作
          let _this = this
          //判断点赞
          let voted = this.article.voted
          console.log(56, voted)
          let addr = this.$store.state.addr
          if(voted.length > 0){
            voted.forEach(item => {
              if(item.toLowerCase() == addr){
                this.votedFlag = true
                console.log(333, "已点赞")
              }
            })
          }

          //判断是否本人
          if(this.article.addr.toLowerCase() == addr){
            this.isMyself = true
            console.log(88899, "是我本人！")
          }

          //获取赞赏金额
          let instance = this.$store.state.postinstance
          async function main() {
            let s = await instance.getMessage()
            _this.level1 = _this.dealNumber(_this.web3.utils.fromWei(s[0], 'ether'))
            _this.level2 = _this.dealNumber(_this.web3.utils.fromWei(s[1], 'ether'))
            _this.level3 = _this.dealNumber(_this.web3.utils.fromWei(s[2], 'ether'))
          }
          main()
        },
        addCollection() {
          let db = this.$store.state.db
          const collection = this.$store.state.username + 'collection'
          // console.log(888, db, '版本号', db.version)

          //得到操作 'collection' 表的事务对象
          let transaction = db.transaction([collection], 'readwrite') //可读可写事务

          transaction.oncomplete = function (event) {
            console.log("事务操作完成")
          }
          transaction.onerror = function (event) {
            console.log("事务操作出错")
            console.dir(event)
          }
          transaction.onsuccess = function (event) {
            console.log('事务操作成功')
          }

          //从事务对象里得到表里的objectStore对象
          let objectStore = transaction.objectStore(collection)
          let post = this.post
          objectStore.add(post)
          // console.log(456, "收藏成功")

          //设置收藏标记
          let collectionFlag = this.$route.params.permlink
          localStorage.setItem(collectionFlag, 'steemjiang')
          this.flag = localStorage.getItem(this.$route.params.permlink)

        },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        },
        dealNumber(money) {
          if (money && money != null) {
            money = String(money)
            let left = money.split('.')[0]
            return left
          }
        },
        voteLevel1(id){
          let _this = this
          let addr = this.$store.state.addr
          let instance = this.$store.state.postinstance
          async function main() {
            let s = await instance.voteLevelOne(id, {from: addr})
            console.log(888888, s)
            _this.votedFlag = true
            //清空vuex中
            _this.$store.commit('saveArticles', [])
          }
          main()
        },
        voteLevel2(id){
          let _this = this
          let addr = this.$store.state.addr
          let instance = this.$store.state.postinstance
          async function main() {
            let s = await instance.voteLevelTwo(id, {from: addr})
            console.log(888888, s)
            _this.votedFlag = true
            //清空vuex中
            _this.$store.commit('saveArticles', [])
          }
          main()
        },
        voteLevel3(id){
          let _this = this
          let addr = this.$store.state.addr
          let instance = this.$store.state.postinstance
          async function main() {
            let s = await instance.voteLevelThree(id, {from: addr})
            console.log(888888, s)
            _this.votedFlag = true
            //清空vuex中
            _this.$store.commit('saveArticles', [])
          }
          main()
        },

      },

      computed: {
        prop () {
          let data = {
            subfield: false,
            defaultOpen: 'preview',
            editable: false,
            toolbarsFlag: false,
            scrollStyle: false,

          }
          return data
        },

      },

      mounted() {
        let that = this
        let instance = this.$store.state.postinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            console.log(777, "instance为空，是刷新页面")
            let instance = await that.getPostInstance()
            await that.getArticles(instance)
            that.articleFilter()
            that.getArticleDone()
            that.isLoading = false
          } else{
            if(that.$store.state.articles == false){
              console.log(666, "如果文章更新！")
              //如果文章更新
              await that.getArticles(instance)
              that.articleFilter()
              that.getArticleDone()
              that.isLoading = false
            } else{
              console.log(2222, "啥也没干！")
              that.articleFilter()
              that.getArticleDone()
              that.isLoading = false
            }
          }
        }
        main()
      },

      filters:{
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },

      },

      components: {
      }
    }
</script>

<style scoped>
  h1{
    font-size: 2rem;
    margin: 1.5rem auto;
    text-align: center;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
  }

  .posttime{
    font-size: 0.8rem;
    text-align: center;
  }

  #content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    width: 60%;
    padding-bottom: 3rem;
    position:relative;
    z-index: 100;
   }

  .v-note-panel{
    background-color: aquamarine !important;
    font-size: 2rem;
    z-index: -1 !important;
  }

  #textclass{
    z-index: auto;
    background-color: aquamarine !important;
  }


  .vote{
    text-align: center;
  }



</style>
