<template>
  <div class="article">
     <div class="content" >

        <p class="posttime"><span style="margin-right: 1.5rem;margin-left: 2rem">卖单号：{{ order.orderNumber }} </span>
            {{ timeFormat(order.created) }}</p>

        <p class="title">{{ order.name }}</p>

         <div class="text">
            买家： {{ order.buyers.length }} 个<br>
             {{ order.buyers }}<br>
            价格： {{ weiToEth(order.price)  }}  灵石<br>
            <p>内容： <span class="neirong">{{ order.content }}</span><br>
                联系方式：<span class="neirong"> {{ order.contact }} </span>
            </p>

             描述：
             <mavon-editor
                     :value="order.body + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
         </div>

         <div class="btn" v-if="!isCancelled">
             <b-button variant="outline-primary" @click="cancerOrder(order.uid)">取消卖单</b-button>
         </div>
         <div v-else class="btn">买单已取消！</div>

         <!--<div v-if="!isMyself">-->
             <!--<div v-if="isPurchased" class="btn2">已购买</div>-->
             <!--<div class="btn" v-else>-->
                 <!--<b-button variant="outline-primary" @click="buy(order.uid)">购买</b-button>-->
             <!--</div>-->
         <!--</div>-->

    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "MysellerUint",
      data(){
          return{
            order: {},
            keywords:'',
            author:'',
            permlink:'',
            isLoading: false,
            isMyself: false,
            isPurchased: false,
            isCancelled: false
          }
      },
      methods:{
          cancerOrder(id){
              let _this = this
              let instance = this.$store.state.ordertinstance
              let addr = this.$store.state.addr
              async function main() {
                let s = await instance.cancerOrder(id, {from: addr})
                console.log(555, s)
                _this.isCancelled = true
                  //将vuex清空
                 _this.$store.commit('saveOrders', [])
              }
              main()
        },
          getDoneMyOrder(){
              let orders = this.$store.state.orders
              orders.forEach(item => {
                  if(item.orderNumber == this.$route.params.orderNumber){
                      this.order = item
                      // console.log(622, '找到啦')
                  }
              })
          },
          getCancelldeFlag(){
              if(this.order.state === '1'){
                  this.isCancelled = true
              }
          }
      },
      components: {
        Loading
      },
      computed: {
            prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },

      mounted() {
            let that = this
            let instance = this.$store.state.ordertinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(8999, "instance为空，是刷新页面")
                    instance = await that.getOrderInstance()
                    console.log(699, instance)
                    await that.getOrders(instance)
                    that.getDoneMyOrder()
                    that.getCancelldeFlag()
                    that.isLoading = false
                } else{
                    if(that.$store.state.orders == false){
                        console.log(667, "如果文章更新！")
                        //如果文章更新
                        await that.getOrders(instance)
                        that.getDoneMyOrder()
                        that.getCancelldeFlag()
                        that.isLoading = false
                    } else{
                        console.log(2233, "啥也没干！")
                        that.getDoneMyOrder()
                        that.getCancelldeFlag()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{

      },
    }
</script>

