import Vue from 'vue'
import Vuex from 'vuex'
import Cookie from 'vue-cookies'

Vue.use(Vuex)

export default new Vuex.Store({

  // 组件中通过 this.$store.state.username 调用
  state: {
    nickname: Cookie.get("nickname"),
    addr: Cookie.get("addr"),

    db:{},

    articles: [],
    postinstance:{},

    orders: [],
    ordertinstance:{},

    walletinstance:{},
    exchangeinstance:{},
    accessinstance:{},
    admins:{},
    adminsLower: {},

    bountyinstance:{},
    bountys: [],

    collections:[],
    searchPosts:[],
    repliestoauthor:[],



  },
  mutations: {
    // 组件中通过 this.$store.commit('saveToken',参数)  调用,只能带一个参数，有多个参数以对象的形式传入
    saveUser: function (state, userobj) {
      state.nickname = userobj.nickname
      state.addr = userobj.addr

      Cookie.set("nickname", userobj.nickname, "1d")
      Cookie.set("addr", userobj.addr, "1d")
    },

    savePostInstance: function (state, instance){
      state.postinstance = instance
    },

    saveArticles: function (state,  articles) {
      state.articles = articles
    },


    saveOrderInstance: function (state, instance){
          state.ordertinstance = instance
      },

    saveOrders: function (state,  orders) {
      state.orders= orders
    },

    saveBountyInstance: function (state, instance){
          state.bountyinstance = instance
      },
    saveBountys: function (state,  bountys) {
      state.bountys= bountys
    },

    saveCollections: function (state,  collections) {
      state.collections= collections
    },

    saveSearchPosts: function (state,  searchPosts) {
      state.searchPosts= searchPosts
    },

    saveDB: function (state, db){
      state.db = db
    },


    saveWalletInstance: function (state, instance){
      state.walletinstance = instance
    },
    saveExchangeInstance: function (state, instance){
      state.exchangeinstance = instance
    },
    saveAccessInstance: function (state, instance){
      state.accessinstance = instance
    },

    saveAdmins: function (state, admins){
      state.admins = admins
    },

    saveAdminsLower: function (state, admins){
      state.adminsLower = admins
    },

    clearUser: function (state) {
      state.nickname = null
      state.addr = null
      Cookie.remove('nickname')
      Cookie.remove('addr')
    }
  }
})

