<template>
  <div class="article">
     <div class="content" >

        <p class="posttime">
            <span style="margin-right: 1.5rem;margin-left: 2rem">
            悬赏号：{{ task.taskNumber }}</span>
            {{ timeFormat(task.created) }}
        </p>

        <p class="title">{{ task.name}}</p>

         <div class="text">
             金主： @{{  task.nickname }} ({{ task.promoter }})<br>
             赏金： {{ weiToEth(task.bonus)  }}  灵石<br>
             联系金主：{{ task.contact }}<br>
             任务描述：
             <mavon-editor
                     :value="task.content + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
             <br>
             <!--任务时间： {{ task.taskDeadLine }}<br>-->

         </div>

         <!--任务申请中-->
         <div v-if="task.state === '0'">
             <span class="btn2">任务申请中...... <br><br>
                <b-button variant="outline-primary" @click="cancelTask">取消任务</b-button>
             </span>

              <b-form-group :label="'已申请的猎人'+key" v-for="(hunter,key) in task.hunters" class="hunter">
                 <b-form-radio v-model="selected" name="some-radios" :value="hunter">{{ hunter }}</b-form-radio>
                 <div class="reason" v-if="task.reasons[key]">
                     <p>申请理由：{{ task.reasons[key].reason }} </p>
                     联系方式：{{ task.reasons[key].hunterContact }}
                 </div>
             </b-form-group>

             <div class="mt-3 hunter" v-if="selected" >中选猎人:
                 <strong>{{ selected }}</strong>
             </div>
             <br>
             <div class="btn3" v-if="selected">
                 <b-button variant="outline-primary" @click="confirmHunter(selected, task.uid)">确认猎人</b-button>
             </div>
             <br>
             <div class="btn4">
                 <!--<b-button variant="outline-primary" @click="cancelTask">取消任务</b-button>-->
             </div>
         </div>

          <!--任务进行中-->
         <div v-else-if="task.state  === '2'">
             <span class="btn2" v-if="leftTime > 0">任务进行中..... </span>
             <div class="mt-3 reason">
                 中选猎人: <strong>{{ task.hunterA}}</strong>
                 <p>申请理由：{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p>联系方式：{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>

             <div v-if="leftTime > 0" class="reason">
                 任务剩余时间： {{ formatSeconds(leftTime) }}
                 <p style="text-align: center;">等待中选猎人提交任务！</p>
             </div>
             <div v-else>任务已超时！
                 <div class="btn">
                     <b-button variant="outline-primary" @click="hunterAOutOfTime(task.uid)">猎人超时未提交，确认任务结束！</b-button>
                 </div>
             </div>
         </div>

         <!--猎人已提交任务-->
         <div v-else-if="task.state  === '3'">
             <div class="mt-3 reason">
                 中选猎人: <strong>{{ task.hunterA}}</strong>
                 <p>申请理由：{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p>联系方式：{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <span class="btn2">猎人已提交任务..... </span>
             <div v-if="leftTime > 0" class="reason">金主确认的剩余时间： {{ formatSeconds(leftTime) }}</div>
             <div class="btn3">
                 <b-button variant="outline-primary" @click="promoterFinished(task.uid)" style="margin-right: 1rem">
                     确认任务完成
                 </b-button>
                 <b-button variant="outline-primary" @click="taskFallback(task.uid)">退回任务</b-button>
             </div>
         </div>

         <!--猎人未提交，任务提前结束-->
         <div v-else-if="task.state  === '4'">
             <div class="mt-3">中选猎人: <strong>{{ task.hunterA }}</strong></div>
             <div class="btn2">猎人未提交，任务提前结束！</div>
         </div>

         <!--任务完成-->
         <div v-else-if="task.state  === '5'">
             <div class="mt-3 reason">
                 中选猎人: <strong>{{ task.hunterA}}</strong>
                 <p>申请理由：{{ getHunterMessage(task.hunterA).reason }}</p>
                 <p>联系方式：{{ getHunterMessage(task.hunterA).hunterContact }}</p>
             </div>
             <div class="btn2">任务圆满完成！</div>
         </div>

         <!--金主未确认，任务自动完成！-->
         <div v-else="task.state  === '6'">
             <div class="mt-3">中选猎人: <strong>{{ task.hunterA }}</strong></div>
             <div class="btn2">金主未确认，任务强制完成！</div>
         </div>

    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
       name: "MyBountyUnit",
      data(){
          return{
            task: {},
            selected: '',
            isLoading: false,
            leftTime:'',
            hunterMessage: {}
          }
      },
      methods:{
         taskFilter(){
              //获取单个任务
              let tasks = this.$store.state.bountys
              let taskNumber = this.$route.params.taskNumber
              tasks.forEach(item => {
                  if(item.taskNumber === taskNumber){
                      this.task = item

                      //任务剩余时间
                      let deadtime = parseInt(item.taskStart) + parseInt(item.taskDeadLine)
                      let timestamp = Date.parse(new Date()) * 1e-3
                      this.leftTime = deadtime - timestamp
                  }
              })

             console.log(123456)

          },
         getHunterMessage(address){
             let task = this.task
             for(let i = 0; i < task.hunters.length; i ++){
                 if(task.hunters[i] === address){
                     console.log(111, "找到啦 ")
                     console.log(1111, task.reasons[i])
                     return task.reasons[i]
                 }else {
                     console.log(222, "没找到啦 ")
                 }
             }
          },
         confirmHunter(selected, id){
             let _this = this
             let addr = this.$store.state.addr
             let instance = this.$store.state.bountyinstance
             async function main() {
                 let s = await instance.confirmHunter(id, selected, {from: addr})
                 console.log(666, s)
                 await _this.getTasks(instance)
                 _this.taskFilter()

                 //直接刷新当前页面
                 // _this.$router.go(0)
             }
             main()
          },
         cancelTask(){
              let _this = this
              let addr = this.$store.state.addr
              let id = this.$route.params.task
              let instance = this.$store.state.bountyinstance
              async function main() {
                  let s = await instance.cancelTask(id, {from: addr})
                  console.log(666, s)
                  await _this.getTasks(instance)
                  _this.taskFilter()
                  //直接刷新当前页面
                  // _this.$router.go(0)
              }
              main()
          },
         promoterFinished(id){
             let _this = this
              let addr = this.$store.state.addr
              let instance = this.$store.state.bountyinstance
              async function main() {
                  let s = await instance.promoterFinished(id, {from: addr})
                  console.log(3366, s)
                  await _this.getTasks(instance)
                  _this.taskFilter()
                  //直接刷新当前页面
                  // _this.$router.go(0)
              }
              main()
          },
         taskFallback(id){
              let _this = this
              let addr = this.$store.state.addr
              let instance = this.$store.state.bountyinstance
              async function main() {
                  let s = await instance.taskFallback(id, {from: addr})
                  console.log(3366, s)
                  await _this.getTasks(instance)
                  _this.taskFilter()
                  //直接刷新当前页面
                  // _this.$router.go(0)
              }
              main()
          },
         hunterAOutOfTime(id){
              let _this = this
              let instance = this.$store.state.bountyinstance
              let addr = this.$store.state.addr
              async function main() {
                  let s = await instance.hunterAOutOfTime(id, {from: addr})
                  console.log(555, s)
                  await _this.getTasks(instance)
                  _this.taskFilter()
                  //直接刷新当前页面
                  // _this.$router.go(0)
              }
              main()
          },
      },
      components: {
        Loading
      },
      computed: {
          prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },


        mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.taskFilter()
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.taskFilter()
                        that.isLoading = false
                    } else{
                        console.log(22333, "啥也没干！")
                        that.taskFilter()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{

      },
    }
</script>

