<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入标题" v-model="title"
    ></b-input>

    <div id="main">
          <mavon-editor v-model="body" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>

    <br>
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0"
      placeholder="输入关键词，以空格分隔" v-model="keywords"
    ></b-input>

    <div class="btn">
      <b-button variant="outline-primary" @click="post">提交</b-button>
    </div>


      <!--<button @click="get">提取内容</button>-->
    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Article",
      data() {
        return {
          db:{},
          body: '',
          title: '',
          keywords: '',
          isLoading: false,
          flag: false
        }
      },

      methods: {
        post(){
            this.flag = false
            this.isLoading = true
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.postinstance
            let addr = this.$store.state.addr
            let permlink = this.getstr()
            let title = this.title
            let body = this.body
            let created = this.getTimeStamp()
            let keywords = this.keywords.trim().split(/\s+/)
            // console.log(11111, keywords) 数组[]
            // console.log(11122,JSON.stringify(keywords))
            // let jsonMetadata = {"keywords": keywords, "dapp": "etherlark", "format": "markdown"}
            let _this = this

            //发布到本地数据库
            //把数据拼成一个JSON
            // let post = {
            //   "title": title,
            //   "author": author,
            //   "category": 'hive-180932',
            //   "permlink": permlink,
            //   "body": body,
            //   "created": new Date().toISOString().split('.')[0],
            //   "json_metadata": JSON.stringify(jsonMetadata)
            // }
          // let db = this.$store.state.db
          // let tableName = this.$store.state.username

          async function main(){
            // await that.saveOneToDb(post, db, tableName)
            let article = {
              "title": title,
              "nickname": nickname,
              "addr": addr,
              "permlink": permlink,
              "body": body,
              'keywords': keywords,
              'created': created
            }

            //上传文章到ipfs
            let content = Buffer.from(JSON.stringify(article))
            let res = await _this.ipfs.add(content)
            let hash = res[0].hash
            // console.log(566, res)
            // console.log(5668, hash)

            let ss = await instance.postArticle(hash, {from: addr})
            // console.log(955, ss)

            //清空vuex中的数据
            _this.$store.commit('saveArticles', [])
            _this.$router.push({path:'/'})
          }
          main()
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.postinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            console.log(777, "instance为空")
            let instance = await that.getPostInstance()
            await that.getArticles(instance)
            that.isLoading = false
            that.flag = true
          } else{
            if(that.$store.state.articles == false){
              console.log(666, "如果文章更新！")
              //如果文章更新
              await that.getArticles(instance)
              that.isLoading = false
              that.flag = true
            } else{
              console.log(2222, "啥也没干！")
              that.isLoading = false
              that.flag = true
            }
          }
        }
        main()
      },
    }
</script>

