             <b-dropdown id="dropdown-grouped" :text="'@'+$store.state.nickname" variant="transparent" class="m-2 nickname">
                <b-dropdown-item-button @click="myarticles">
                  我的文章
                </b-dropdown-item-button>
                <b-dropdown-divider></b-dropdown-divider>
                 <b-dropdown-item-button @click="myseller">
                     我是卖家
                 </b-dropdown-item-button>
                 <b-dropdown-divider></b-dropdown-divider>
                 <b-dropdown-item-button @click="mybuyer">
