                 <div v-if="ownerLower === $store.state.addr">
                     <b-dropdown-item-button @click="admin">
                         我是界主
                     </b-dropdown-item-button>
                     <b-dropdown-divider></b-dropdown-divider>
                 </div>
                 <div v-else-if="superadminsLower.indexOf($store.state.addr) > -1">
                     <b-dropdown-item-button @click="admin">
                         我是太上
                     </b-dropdown-item-button>
