<script>
  import Footer from './components/Footer'
  export default {
    name: 'App',
    data(){
      return {
        parmsg: '这是父数据',
        owner: '',
        superadmins: [],
        admins: [],
        ownerLower: '',
        superadminsLower: [],
        adminsLower: [],
      }
    },
    methods: {
      backtoindex(){
        this.$router.push({path: '/'})
      },
      myarticles(){
        this.$router.push({path: '/myarticles'})
      },
      myseller(){
        this.$router.push({path: '/myseller'})
      },
      mybuyer(){
        this.$router.push({path: '/mybuyer'})
      },
      mybounty(){
        this.$router.push({path: '/mybounty'})
      },
      mytask(){
        this.$router.push({path: '/mytask'})
      },
      mywallet(){
            this.$router.push({path: '/wallet'})
        },
      admin(){
            this.$router.push({path: '/admin'})
        },
      async getAdmin(){
            let instance = this.$store.state.accessinstance
            let res = await instance.getAdmins()
            let admin = {}
            this.owner = admin.owner = res[0]
            this.superadmins = admin.superadmins = res[1]
            this.admins = admin.admins = res[2]
            // console.log(555, admin)
            this.$store.commit('saveAdmins', admin)
        },
      addrToLower(){
            let admin = {}
            this.ownerLower = admin.ownerLower = this.owner.toLowerCase()
            for(let i = 0; i < this.superadmins.length; i ++){
                this.superadminsLower.push(this.superadmins[i].toLowerCase())
            }
            admin.superadminsLower = this.superadminsLower
            for(let i = 0; i < this.admins.length; i ++){
                this.adminsLower.push(this.admins[i].toLowerCase())
            }
          admin.adminsLower = this.adminsLower
          // console.log(666, admin)
          this.$store.commit('saveAdminsLower', admin)
        },
      logout(){
        this.$store.commit('clearUser')
      }
    },
    components: {
      Footer,
    },
    mounted() {
          let that = this
          async  function main(){
              await that.getAccessInstance()
              await that.getAdmin()
              that.addrToLower()
              that.isLoading = false
              that.flag = true
          }
          main()
      },

  }
</script>

