<template>
  <div class="login">
    <h3 v-if="ownerLower === $store.state.addr">欢迎您，界主大人！</h3>
    <h3 v-else-if="superadminsLower.indexOf($store.state.addr) > -1">欢迎您，太上大人！</h3>
    <h3 v-else-if="adminsLower.indexOf($store.state.addr) > -1">欢迎您，长老大人！</h3>
    <h3 v-else></h3>

    <div v-if="flag">
      呢称： {{ $store.state.nickname }}<br>
      地址： {{ $store.state.addr }} <br>

      <div v-if="ownerLower === $store.state.addr">
        <br>
        设置太上:
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="太上地址" v-model="newSuperAdmin"
        ></b-input>
        <div class="loginbtn">
          <span style="margin-right: 1rem">
          <b-button variant="outline-primary" @click="setSuperAdmin">确认添加太上</b-button>
          </span>
          <b-button variant="outline-primary" @click="delSuperAdmin">删除太上</b-button>
        </div>
      </div>
      <br><br>

      <div v-if="ownerLower === $store.state.addr || superadminsLower.indexOf($store.state.addr) > -1">
        <div >
          设置长老:
          <b-input
                  class="mb-2 mr-sm-2 mb-sm-0 user"
                  placeholder="长老地址" v-model="newAdmin"
          ></b-input>
          <div class="loginbtn">
            <span style="margin-right: 1rem">
            <b-button variant="outline-primary" @click="setAdmin">确认添加长老</b-button>
            </span>
            <b-button variant="outline-primary" @click="delAdmin">删除长老</b-button>
          </div>
        </div>


        <div v-if="ownerLower === $store.state.addr"><br><br>
          灵石总量： {{ totalSupply }} <br>
          设置增发:
          <b-input
                  class="mb-2 mr-sm-2 mb-sm-0 user"
                  placeholder="设置增发量" v-model="addAmount"
          ></b-input>
          <div class="loginbtn">
            <b-button variant="outline-primary" @click="addSupply">确认增发</b-button>
          </div>
        </div>
        <br><br>

        <div>
          兑换率： {{ price }} （1以太币 = {{ price }}灵石 ）<br>
          重设汇率:
          <b-input
                  class="mb-2 mr-sm-2 mb-sm-0 user"
                  placeholder="输入新汇率" v-model="newPrice"
          ></b-input>
          <div class="loginbtn">
            <b-button variant="outline-primary" @click="setPrice">重设汇率 </b-button>
          </div>
        </div>
        <br><br>
      </div>

      <div style="width:80%;">
         <h4>界会名单：</h4>
        界主：{{ $store.state.admins.owner}}<br><br>
        太上：{{ $store.state.admins.superadmins }}<br><br>
        长老：{{ $store.state.admins.admins }}<br>
      </div>

    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Admin",
    data() {
      return {
        owner: '',
        superadmins: [],
        admins: [],
        ownerLower: '',
        superadminsLower: [],
        adminsLower: [],
        newSuperAdmin:'',
        newAdmin: '',
        isLoading: false,
        flag: false,
        addAmount: '',
        price:'',
        newPrice: '',
        totalSupply: '',
      }
    },
    methods: {
      async getAdmin(){
        let instance = this.$store.state.accessinstance
        let res = await instance.getAdmins()
        let admin = {}
        this.owner = admin.owner = res[0]
        this.superadmins = admin.superadmins = res[1]
        this.admins = admin.admins = res[2]
        // console.log(555, admin)
        this.$store.commit('saveAdmins', admin)
      },
      addrToLower(){
        let admin = {}
        this.ownerLower = admin.ownerLower = this.owner.toLowerCase()
        for(let i = 0; i < this.superadmins.length; i ++){
          this.superadminsLower.push(this.superadmins[i].toLowerCase())
        }
        admin.superadminsLower = this.superadminsLower
        for(let i = 0; i < this.admins.length; i ++){
          this.adminsLower.push(this.admins[i].toLowerCase())
        }
        admin.adminsLower = this.adminsLower
        // console.log(666, admin)
        this.$store.commit('saveAdminsLower', admin)
      },
      getAdminLower(){
        let admin = this.$store.state.adminsLower
        this.ownerLower = admin.ownerLower
        this.superadminsLower = admin.superadminsLower
        this.adminsLower = admin.adminsLower
      },
      async setSuperAdmin(){
        this.flag = false
        this.isLoading = true
        let instance = this.$store.state.accessinstance
        let addr = this.$store.state.addr
        let res = await instance.addSuperAdmin(this.newSuperAdmin, {from: addr})
        console.log(128999, res)
        await this.getAdmin()
        this.addrToLower()
        this.newSuperAdmin = ''
        this.isLoading = false
        this.flag = true
      },
      async delSuperAdmin(){
        this.flag = false
        this.isLoading = true
        let instance = this.$store.state.accessinstance
        let addr = this.$store.state.addr
        let res = await instance.delSuperAdmin(this.newSuperAdmin, {from: addr})
        console.log(128999, res)
        await this.getAdmin()
        this.addrToLower()
        this.newSuperAdmin = ''
        this.isLoading = false
        this.flag = true
      },
      async setAdmin(){
        this.flag = false
        this.isLoading = true
        let instance = this.$store.state.accessinstance
        let addr = this.$store.state.addr
        let res = await instance.addAdmin(this.newAdmin, {from: addr})
        console.log(22299, res)
        await this.getAdmin()
        this.addrToLower()
        this.newAdmin = ''
        this.isLoading = false
        this.flag = true
      },
      async delAdmin(){
        this.flag = false
        this.isLoading = true
        let instance = this.$store.state.accessinstance
        let addr = this.$store.state.addr
        let res = await instance.delAdmins(this.newAdmin, {from: addr})
        console.log(22299, res)
        await this.getAdmin()
        this.addrToLower()
        this.newAdmin = ''
        this.isLoading = false
        this.flag = true
      },
      async addSupply() {
        this.flag = false
        this.isLoading = true
        let value = this.addAmount  //增发的量
        let value3 = this.web3.utils.toWei(value, 'ether')
        let addr = this.$store.state.addr
        let instance = this.$store.state.walletinstance
        await instance.addSupply(value3, {from: addr})
        await this.getBalance()
        await this.getExchangeDone()
        this.addAmount = ''
        this.isLoading = false
        this.flag = true
      },
      async setPrice() {
        // let price = 18000  //重设价格 1eth = 18000 els
        this.flag = false
        this.isLoading = true
        let addr = this.$store.state.addr
        let instance = this.$store.state.exchangeinstance
        await instance.setPrice(this.newPrice, {from: addr})
        await this.getBalance()
        await this.getExchangeDone()
        this.newPrice = ''
        this.isLoading = false
        this.flag = true
      },
      async getBalance(){
        let instance = this.$store.state.walletinstance
        let b = await instance.totalSupply()
        this.totalSupply = this.weiToEth(b)
      },
      async getExchangeDone(){
        let instance2 = this.$store.state.exchangeinstance
        //兑换价格
        let a5 = await instance2.getMessage()
        this.price = a5[1]
      },
    },

    components: {
        Loading
      },
    mounted() {
      let that = this
      let instance = this.$store.state.walletinstance
      async  function main(){
        if(Object.keys(instance).length === 0){
          //如果刷新页面, instance未定义
          // console.log(888, "instance为空，是刷新页面")
          await that.getWalletInstance()
          await that.getExchangeInstance()
          that.getAdminLower()
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        } else{
          console.log(22333, "啥也没干！")
          that.getAdminLower()
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        }
      }
      main()
    },

    }
</script>

<style scoped>

  .login{
    margin: 4rem 4rem 18rem 14rem;

    /*background-color: aqua;*/
  }
  .user{
    margin-top: 1rem;
    width: 30rem;
  }
  .user2{
    display: inline;
    width: 8rem;

  }

  .exchange{
    width: 30rem;
  }
  .loginbtn{
    margin-top: 0.5rem;

  }
</style>

