<template>
  <div class="article">
     <div class="content" >

        <p class="posttime"><span style="margin-right: 1.5rem;margin-left: 2rem">卖单号：{{ order.orderNumber }} </span>
            {{ order.created | changeTime}}</p>

        <p class="title">{{ order.name }}</p>

         <div class="text">
            卖家： @{{  order.nickname }}({{ order.addr }})<br>
            买家： {{ order.buyers.length }} 个<br>
             价格： {{ weiToEth(order.price) }}  灵石<br>
            <p v-if="isPurchased" >
                内容： <span class="neirong">{{ order.content }}</span><br>
                联系卖家：<span class="neirong"> {{ order.contact }} </span>
            </p>
             <p v-else style="display: inline;">
                 内容： <span class="neirong2">已封印！请购买以解除封印！</span><br>
             </p>
             <!--描述： {{ order[6] }}<br>-->
             描述：
             <mavon-editor
                     :value="order.body + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
         </div>

         <div v-if="!isMyself">
             <div v-if="isPurchased" class="btn2">已购买</div>
             <div class="btn" v-else>
                 <b-button variant="outline-primary" @click="buy(order.uid)">购买</b-button>
             </div>
         </div>

    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Order",
      data(){
          return{
            order: [],
            keywords:'',
            author:'',
            permlink:'',
            isLoading: false,
            isMyself: false,
            isPurchased: false
          }
      },
      methods:{
          buy(id){
              let _this = this
              let instance = this.$store.state.ordertinstance
              let addr = this.$store.state.addr
              async function main() {
                let s = await instance.confirmPurchase(id, {from: addr})
                console.log(555, s)
                _this.isPurchased = true
                  //将vuex清空
                 _this.$store.commit('saveOrders', [])
              }
              main()
        },
          getOrderDone(){
              let orders = this.$store.state.orders
              let addr = this.$store.state.addr
              orders.forEach(item => {
                  if(item.orderNumber === this.$route.params.orderNumber){
                      this.order = item
                      // console.log(622, '找到啦')
                  }
              })

              //判断是否本人
              if(this.order.addr.toLowerCase() === addr){
                  this.isMyself = true
                  console.log(88899, "是我本人！")
              }

              //判断是否已购买
              let buyers = this.order.buyers
              buyers.forEach(item => {
                  if(item.toLowerCase() == addr){
                      console.log(566, "已购买")
                      this.isPurchased = true
                  }
              })
          }
      },


      components: {
        Loading
      },
      computed: {
            prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },

        mounted() {
            let that = this
            let instance = this.$store.state.ordertinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(868, "instance为空，是刷新页面")
                    instance = await that.getOrderInstance()
                    await that.getOrders(instance)
                    that.getOrderDone()
                    that.isLoading = false
                } else{
                    if(that.$store.state.orders == false){
                        console.log(667, "如果文章更新！")
                        //如果文章更新
                        await that.getOrders(instance)
                        that.getOrderDone()
                        that.isLoading = false
                    } else{
                        console.log(2233, "啥也没干！")
                        that.getOrderDone()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{
        changeTime(timestamp){
              // 比如需要这样的格式 yyyy-MM-dd hh:mm:ss
              let date = new Date(timestamp * 1000)
              let Y = date.getFullYear() + '-'
              let  M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
              let D = date.getDate() + ' '
              let h = date.getHours() + ':'
              let m = date.getMinutes() + ':'
              let s = date.getSeconds()
              return Y+M+D+h+m+s
          },
      },
    }
</script>

