<template>
  <div class="article">
     <div class="content" >

        <p class="posttime"><span style="margin-right: 1.5rem;margin-left: 2rem">悬赏号：{{ task.taskNumber }} </span>
            {{ timeFormat(task.created) }}
        </p>
        <p class="title">{{ task.name }}</p>
         <div class="text">
            金主： @{{  task.nickname }} ({{ task.promoter }})<br>
             <span v-if="task.bonus">赏金： {{ weiToEth(task.bonus) }}  灵石</span><br>
             任务描述：
             <mavon-editor
                     :value="task.content + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
             任务时间： {{ formatSeconds(task.taskDeadLine) }}<br>
         </div>

         <!--任务申请中-->
         <div v-if="task.state === '0'" class="reason">
             <span v-if="task.hunters != false">
                 已申请的猎人名单：<br>
                 {{ task.hunters }}</span><br><br>
             <span class="btn2">任务申请中......</span>

           <div v-if="isPromoter">
               <!--<span class="btn2">您是金主！</span>-->
           </div>
           <div v-else>
              <div v-if="!isApplied" class="reason">
                  <b-form-textarea
                          class="reason2"
                          v-model="reason"
                          placeholder="请输入申请的理由，仅金主可见！"
                          rows="4"
                          max-rows="9"
                  ></b-form-textarea>
                  <br>
                  <b-input
                          class="mb-2 mr-sm-2 mb-sm-0 reason2"
                          placeholder="请输入联系方式，仅金主可见。"  v-model="hunterContact"
                  ></b-input>
                  <br>
                  <b-button variant="outline-primary" @click="apply(task.uid)">申请任务</b-button>
              </div>
              <div v-else class="btn2">您已申请！</div>
          </div>
      </div>

         <!--任务进行中-->
        <div v-else-if="task.state  === '2'">
         中选猎人： {{ task.hunterA }} <br>
         任务进行中222.....
     </div>


    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Task",
      data(){
          return{
            task: {},
            reason: '',
            hunterContact: '',
            isLoading: false,
            isApplied: false,
            isPromoter: false,
          }
      },
      methods:{
         apply(id){
          let _this = this
          let instance = this.$store.state.bountyinstance
          let addr = this.$store.state.addr
          let reason = this.reason
          let hunterContact = this.hunterContact
          async function main() {
            let hunterReason = {
                  "reason": reason,
                  "hunterContact": hunterContact
              }
            //上传内容到ipfs
            let con = Buffer.from(JSON.stringify(hunterReason))
            let res = await _this.ipfs.add(con)
            let hash = res[0].hash

            let s = await instance.applyTask(id, hash, {from: addr})
            console.log(555, s)
            _this.isApplied = true
              //清空vuex中的数据
              _this.$store.commit('saveBountys', [])
              // _this.$router.push({path:'/bounty'})
          }
          main()
        },
         taskFilter(){
              //获取单个任务
              let tasks = this.$store.state.bountys
              let taskNumber = this.$route.params.taskNumber
              tasks.forEach(item => {
                  if(item.taskNumber === taskNumber){
                      this.task = item
                  }
              })
          },
         getTaskDone(){
             let addr = this.$store.state.addr
              //判断是否已申请任务
              let hunters = this.task.hunters
              hunters.forEach(item => {
                  if(item.toLowerCase() == addr){
                      console.log(566, "已申请")
                      this.isApplied = true
                  }
              })

              //判断是不是金主 isPromoter
              if(this.task.promoter.toLowerCase() === this.$store.state.addr){
                  this.isPromoter = true
              }
          }
      },


      components: {
        Loading
      },
      computed: {
            prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },

      mounted() {
            let that = this
            let instance = this.$store.state.bountyinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //如果刷新页面, instance未定义
                    console.log(888, "instance为空，是刷新页面")
                    let instance = await that.getBountyInstance()
                    await that.getTasks(instance)
                    that.taskFilter()
                    that.getTaskDone()
                    that.isLoading = false
                } else{
                    if(that.$store.state.bountys == false){
                        console.log(6647, "如果悬赏更新！")
                        //如果文章更新
                        await that.getTasks(instance)
                        that.taskFilter()
                        that.getTaskDone()
                        that.isLoading = false
                    } else{
                        console.log(22333, "啥也没干！")
                        that.taskFilter()
                        that.getTaskDone()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{

      },
    }
</script>

