<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入名称" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              placeholder="请输入赏金（灵石数）" v-model="bonus"
      ></b-input>
      <strong style="color: rgba(240,56,6,0.75)">加上押金 {{ bonus/2 }} 灵石</strong>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder="请输入联系方式，只有中标的猎人可见。"  v-model="contact"
      ></b-input>

      任务描述：
    <div id="main">
          <mavon-editor v-model="content" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>

      <br>
      设定任务时间：
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              placeholder="请输入小时" v-model="hour"
      ></b-input>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              placeholder="请输入分钟" v-model="minute"
      ></b-input>
      <br>


    <div class="btn">
      <b-button variant="outline-primary" @click="post">提交</b-button>
    </div>


      <!--<button @click="get">提取内容</button>-->
    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostOrder",
      data() {
        return {
          db:{},
          content: '',
          name: '',
          contact: '',
          bonus: '',
          hour: '',
          minute: '',
          isLoading: false,
          flag: false
        }
      },

      methods: {
        post(){
            this.flag = false
            this.isLoading = true
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.bountyinstance
            let addr = this.$store.state.addr
            let name= this.name
            let taskNumber = this.getNum()
            let bonus= this.bonus
            let contact = this.contact
            let content = this.content
            let hour = this.hour
            let minute = this.minute
            let created = this.getTimeStamp()
            let _this = this

            async function main(){
              let value = _this.web3.utils.toWei(bonus, 'ether')
              // console.log(699, bonus)

              let task = {
                "name": name,
                "nickname": nickname,
                "taskNumber": taskNumber,
                "contact": contact,
                "content": content,
                'bonus': value,
                'created': created
              }

              //上传内容到ipfs
              let con = Buffer.from(JSON.stringify(task))
              let res = await _this.ipfs.add(con)
              let hash = res[0].hash

              let s = await instance.setTask(hash, value, hour, minute, {from: addr})
              // console.log(555, s)

              //清空vuex中的数据
              _this.$store.commit('saveBountys', [])
              _this.$router.push({path:'/bounty'})
            }
            main()
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }
      },

      components: {
        Loading
      },

      mounted() {//逻辑稍有问题
        let that = this
        let instance = this.$store.state.bountyinstance
        let addr = this.$store.state.addr
        // console.log(32221, instance)

        async function main(){
          if(that.$store.state.nickname){
            that.flag = true
            //如果已登录
            if(Object.keys(instance).length === 0){
              //如果刷新
              // console.log(777777, "instance未定义")
              let res = await that.axios.get('Bounty.json')
              // console.log(667, res.data)
              let contract = that.TruffleContract(res.data)
              // console.log(77, contract)
              contract.setProvider(that.web3.currentProvider)
              let instance = await contract.deployed()
              // console.log(31111, instance)
              //将得到的数据存入vuex中
              that.$store.commit('saveBountyInstance', instance)
              that.flag = true
            }else{
              //instance已定义好
              // console.log(88888, "instance已定义")
              that.flag = true
            }

          }
        }
        main()
      }
    }
</script>

<style scoped>
  #maintop{
    width: 90%;
    height: 100%;
    margin: 0 auto;
  }

  #main{
    height: 30rem;
  }
  .title{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
  }
  .content{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
    background-color: bisque;
  }
  .hour{
    display: inline;
    width: 20%;
  }
  .btn{
    margin-top: 0.3rem;
  }
