<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="请输入商品名称" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              placeholder="请输入价格：灵石" v-model="price"
      ></b-input>
      <b-form-textarea
              class="content"
              v-model="content"
              placeholder="请输入电子商品的链接或网盘地址，买家付费后可见！"
              rows="3"
              max-rows="6"
      ></b-form-textarea>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder="请输入联系方式(建议邮箱)，买家付费后可见。"  v-model="contact"
      ></b-input>

      商品描述：
    <div id="main">
          <mavon-editor v-model="body" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>


    <div class="btn">
      <b-button variant="outline-primary" @click="post">提交</b-button>
    </div>


      <!--<button @click="get">提取内容</button>-->
    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostOrder",
      data() {
        return {
          db:{},
          body: '',
          content: '',
          name: '',
          price: '',
          contact: '',
          isLoading: false,
          flag: false
        }
      },

      methods: {
        post(){
            this.flag = false
            this.isLoading = true
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.ordertinstance
            let addr = this.$store.state.addr
            let name = this.name
            let orderNumber = this.getNum()
            let price = this.price
            let content = this.content
            let contact = this.contact
            let body = this.body
            let created = this.getTimeStamp()
            let _this = this

            async function main(){
              let value = _this.web3.utils.toWei(price, 'ether')
              // console.log(699, value)

              let order = {
                "name": name,
                "nickname": nickname,
                "addr": addr,
                "orderNumber": orderNumber,
                "content": content,
                "contact": contact,
                "body": body,
                'price': value,
                'created': created
              }

              //上传内容到ipfs
              let con = Buffer.from(JSON.stringify(order))
              let res = await _this.ipfs.add(con)
              let hash = res[0].hash

              let s = await instance.postOrder(hash, value, {from: addr})
              // console.log(555, s)

              //清空vuex中的数据
              _this.$store.commit('saveOrders', [])
              _this.$router.push({path:'/market'})
            }
            main()
          },
        get(){
          let _this = this
          let hash = 'QmZEBDS4Sh9Yhr48rUSLKMbyPDsr4rvRZXmAsfwEVfgHtG'
          async function main(){
            let res = await _this.ipfs.cat(hash)
            // console.log(1234, res)

            let t = _this.utf8ArrayToStr(res)
            // console.log(688, t)
          }
          main()

        },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.ordertinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //如果刷新页面, instance未定义
            console.log(888, "instance为空，是刷新页面")
            let instance = await that.getOrderInstance()
            await that.getOrders(instance)
            that.isLoading = false
            that.flag = true
          } else{
            if(that.$store.state.orders == false){
              console.log(667, "如果文章更新！")
              //如果文章更新
              await that.getOrders(instance)
              that.isLoading = false
              that.flag = true
            } else{
              console.log(2233, "啥也没干！")
              that.isLoading = false
              that.flag = true
            }
          }
        }
        main()
      },
    }
</script>

