import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import ipfsClient from 'ipfs-http-client'
import axios from 'axios'
import TruffleContract from 'truffle-contract'
import Web3 from 'web3'
import abi from 'ethereumjs-abi'
import blacklists from "../public/blackLists.json"


import {getstr} from "./utils/getstr"
import {getNum} from "./utils/getNum"
import {timeFormat} from "./utils/timeFormat"
import {formatSeconds} from "./utils/formatSeconds"
import {getTimeStamp} from "./utils/getTimeStamp"
import {utf8ToStr} from "./utils/utf8ToStr"
import {weiToEth} from "./utils/weiToEth"
import {getPostInstance} from "./utils/getPostInstance"
import {getArticles} from "./utils/getArticles"
import {getOrderInstance} from "./utils/getOrderInstance"
import {getOrders} from "./utils/getOrders"
import {getBountyInstance} from "./utils/getBountyInstance"
import {getTasks} from "./utils/getTasks"
import {getWalletInstance} from "./utils/getWalletInstance"
import {getExchangeInstance} from "./utils/getExchangeInstance"
import {getAccessInstance} from "./utils/getAccessInstance"
import {imgAdd} from "./utils/imgAdd"
import {initDatabase} from "./utils/initDatabase"
import {getAllPosts} from "./utils/getAllPosts"
import {getCollections} from "./utils/getCollections"
import {saveOneToDb} from "./utils/saveOneToDb"

Vue.use(BootstrapVue)
Vue.use(mavonEditor)

//获取黑名单
let blackLists = blacklists.trim().split(/\s+/)

let web3
let web3Provider
if (typeof window.ethereum !== 'undefined'){
  console.log(111, "metamask")
  web3Provider = window.ethereum
  // console.log(122, web3Provider)
  web3 = new Web3(window.ethereum)
  // console.log(123, web3)
} else {
  console.log(222, "无metamask")
  alert("没有安装metamask！\n 请安装！ https://metamask.io ")

  // set the provider you want from Web3.providers
  // web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"))
}

//设置ipfs节点, ,挂载到全局
const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9001', protocol: 'https' })
Vue.prototype.ipfs = ipfs


const ethapi = require('etherscan-api').init('DYX36PF9I32G7TDQDFF97CSK24SXWZXIN9','ropsten',  '3000')


Vue.prototype.getstr = getstr
Vue.prototype.getNum = getNum
Vue.prototype.timeFormat = timeFormat
Vue.prototype.formatSeconds = formatSeconds
Vue.prototype.getTimeStamp = getTimeStamp
Vue.prototype.getPostInstance = getPostInstance
Vue.prototype.getArticles = getArticles
Vue.prototype.getOrderInstance = getOrderInstance
Vue.prototype.getOrders = getOrders
Vue.prototype.getBountyInstance = getBountyInstance
Vue.prototype.getTasks = getTasks
Vue.prototype.getWalletInstance = getWalletInstance
Vue.prototype.getExchangeInstance = getExchangeInstance
Vue.prototype.getAccessInstance = getAccessInstance
Vue.prototype.utf8ToStr = utf8ToStr
Vue.prototype.weiToEth = weiToEth
Vue.prototype.imgAdd = imgAdd
Vue.prototype.initDatabase = initDatabase
Vue.prototype.getAllPosts = getAllPosts
Vue.prototype.getCollections = getCollections
Vue.prototype.saveOneToDb = saveOneToDb
Vue.prototype.TruffleContract = TruffleContract
Vue.prototype.web3 = web3
Vue.prototype.web3Provider= web3Provider
Vue.prototype.axios  = axios
Vue.prototype.ethapi  = ethapi
Vue.prototype.abi  = abi
Vue.prototype.blackLists  = blackLists

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

router.beforeEach(function (to, from, next) {
  if(to.meta.requireAuth){
    // 要去的url只有登陆成功后才能访问
    if (store.state.nickname) {
      next()
    } else {
      next({name: 'login'})
    }
  }else{
    next()
  }
})

