const getOrders = function(instance){
  let that = this
  return new Promise(resolve => {
    async function main(){
      let s = await instance.id()
      let orders = []
      for(let i = s; i > 0 ; i --){
        let s1 = await instance.getOrder(i)
        let con = await that.ipfs.cat(s1[0])
        let t = that.utf8ToStr(con)
        let order = JSON.parse(t)
        order.uid = i
        order.buyers = s1[2]
        order.state = s1[4]
        // console.log(56000, order)
        orders.push(order)
      }
      //将得到的数据存入vuex中
      that.$store.commit('saveOrders', orders)
      resolve()
    }
    main()
  })
}

export {getOrders}
